import { Component, OnInit } from '@angular/core';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { RuleInfoBean } from '../bean/rule-info.bean';
import { tableMessage } from '../../../../../@uisftech/common/constant/config';
import { Message, ConfirmationService } from 'primeng/primeng';
import { FileCenterHttpService } from '../http/file-center.http.service';
@Component({
    selector: 'rule-info',
    templateUrl: './rule-info.component.html',
    styleUrls: ['./rule-info.component.scss'],
    providers: [ConfirmationService, FileCenterHttpService]
})
// 
export class RuleInfoComponent implements OnInit {
    tabMesg: string = tableMessage;//
    ruleInfoBean: RuleInfoBean = new RuleInfoBean();//
    msgs: Message[] = [];//
    list: any = [];//
    detailDisplay: boolean = false;//
    addDisplay: boolean = false; //
    updateDisplay: boolean = false;//
    currentPage: any;//
    rows: any;//
    first: number = 0;//
    total: any; //
    inValue: any;
    inValueDetail: any;
    code:any;
    Judge:any=[];
    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public fileCenterHttpService: FileCenterHttpService,
        public confirmationService: ConfirmationService
    ) {
        this.codeValues();
        this.Judge=this.code['JUDGE'];//
    }

    ngOnInit() {
        this.ruleInfoBean.pageNum = 1;
        this.ruleInfoBean.pageSize = 10;
        this.query();
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }

    //
    query() {
        this.ruleInfoBean.pageNum = 1;
        this.ruleInfoBean.pageSize = 10;
        this.queryClick();

    }

    queryClick() {
        this.fileCenterHttpService.findDocumentRules(this.ruleInfoBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.total = data.totalNum;
                this.list = data.rulesList;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })
    }
    //
    reset() {
        this.ruleInfoBean = new RuleInfoBean();
        this.ruleInfoBean.pageNum = 1;
        this.ruleInfoBean.pageSize = 10;
        this.querySecleReset();
    }
    //
    querySecleReset() {
        this.ruleInfoBean.pageSize = 10;
        this.ruleInfoBean.pageNum = 1;
        this.first = 0;
    }
    //
    add() {
        this.addDisplay = true;
    }

    //
    addCall(event) {
        this.addDisplay = event;
        this.queryClick();
    }

    //
    check(col) {
        this.detailDisplay = true;
        this.inValueDetail = col;
    }

    //
    update(col) {
        this.updateDisplay = true;
        this.inValue = col;
    }
    // 
    updateCall(event) {
        this.updateDisplay = event;
        this.queryClick();
    }
    //
    paginate(event) {
        //
        this.ruleInfoBean.pageSize = Number(event.rows);
        //
        let currentPage = event.page + 1;
        this.ruleInfoBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.ruleInfoBean.pageSize;
        this.queryClick();//
    }

    //
    detele(item) {
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                let param = {
                    drId: item.drId,
                }
                this.fileCenterHttpService.deleteDocumentRules(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                        this.queryClick();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                })
            }
        });
    }
}