import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { Message } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
import { FileCenterHttpService } from '../../http/file-center.http.service';

@Component({
    selector: 'field-list-detail',
    templateUrl: './field-list-detail.component.html',
    styleUrls: ['./field-list-detail.component.scss'],
    providers: [FileCenterHttpService]
})
// 

export class FieldListDeatilComponent implements OnInit {
    @Input() inValue: any;
    mesg: any;//
    msgs: Message[] = [];//
    items: MenuItem[];//
    activeIndex: number;
    vlaue: any;//div
    tableList2: any = [];
    daField: string;
    detail:any={};
    code:any;
    FiledType:any=[];
    FiledView:any;
    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public fileCenterHttpService: FileCenterHttpService
    ) {
        this.codeValues();
        this.FiledType = this.code['FiledType'];//
        this.FiledView = this.code['filedView1'];//
    }

    ngOnInit() {
        this.daField = this.inValue;
        this.echoClick();
    }
    //
    echoClick() {
        let param = {
            daField: this.daField
        }
        this.fileCenterHttpService.queryAttrItemInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.detail=data;
                this.tableList2=data.fieldlist
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
}