import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Message, MenuItem, SelectItem } from 'primeng/primeng';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { tableMessage } from '../../../../../../@uisftech/common/constant/config';
import { FileTemplateCreateBean } from './bean/file-template-create.bean';
import * as _ from 'lodash';
import { FileCenterHttpService } from '../../http/file-center.http.service';

@Component({
	selector: 'file-template-create',
	templateUrl: './file-template-create.component.html',
	styleUrls: ['./file-template-create.component.scss'],
	providers: [FileCenterHttpService]
})
//
export class FileTemplateCreateComponent implements OnInit {

	mesg: any;//
	msgs: Message[] = [];//
	tabMesg: string = tableMessage;//
	fileTemplateCreateBean: FileTemplateCreateBean = new FileTemplateCreateBean();//
	fileType: any;//
	items: MenuItem[];//
	itemes: MenuItem[];//
	activeIndex: number;
	activeIndexs: number;
	vlaue: any;//div
	flag: number;//
	Flag: boolean = false;//
	code: any;//
	oneChooseList: any = [];//-
	allChooseList: boolean = false;//
	detailDisplay: boolean = false;//
	detail: any = {};//
	custChooseDisplay: boolean = false;
	list: any = [];//
	arr: any = [];//
	tableList1: any = [];//-
	tableList2: any = [];//-
	FileTemplateCate: any = [];//
	FileTemplateCates: any = [];//
	DtType: any = [];//
	flagUpdate: string;
	dtId: string;//
	inValue: any;
	FiledType: any = [];
	Judge: any = [];
	drName: string;
	drEntityField: string;
	checkBox: any = [];
	checkBox1: any = [];
	chooseData: any = [];
	chooseData1: any = [];
	aa: any;
	showStep: boolean = true;//
	compWidth: string = '16%';
	constructor(
		public commfunc: Commfunc,
		public router: Router,
		public activatedRoute: ActivatedRoute,
		public fileCenterHttpService: FileCenterHttpService
	) {
		this.mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		this.codeValues() //
		this.DtType = this.code['DtType'];//
		this.FileTemplateCate = this.code['FileTemplateCate'];//
		this.FileTemplateCates = this.code['FileTemplateCates'];//
		this.FiledType = this.code['FiledType'];//
		this.Judge = this.code['JUDGE'];//
		activatedRoute.queryParams.subscribe(queryParams => {
			this.flagUpdate = queryParams.flag;
		})
		if (this.flagUpdate) {
			this.dtId = this.activatedRoute.snapshot.params['dtId']
		}
	}

	//
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code
	}

	ngOnInit() {
		this.activeIndex = 0;
		this.activeIndexs = 0;
		this.vlaue = '0';
		this.flag = 0;
		this.items = [
			{
				label: '',
				command: (event: any) => {
					this.activeIndex = 0;
				}
			},
			{
				label: '',
				command: (event: any) => {
					this.activeIndex = 1;
				}
			},
			{
				label: '',
				command: (event: any) => {
					this.activeIndex = 2;
				}
			},
			{
				label: '',
				command: (event: any) => {
					this.activeIndex = 3;
				}
			}
		];
		this.itemes = [
			{
				label: '',
				command: (event: any) => {
					this.activeIndexs = 0;
				}
			},
			{
				label: '',
				command: (event: any) => {
					this.activeIndexs = 1;
				}
			},
		];
		if (this.flagUpdate == '1') {
			this.updateDetail();
		}
		this.fileTemplateCreateBean.dtFiletype = '.xls';//-excel
		this.fileTemplateCreateBean.dtType = '03';//
	}

	//
	addTemplate() {
		this.custChooseDisplay = true;
		this.inValue = this.dtId;

	}

	//
	delete(col, i) {
		// console.log(col);
		this.list.splice(this.list[i], 1);
		this.list = _.clone(this.list);
	}

	toRuleInfo(col) {
		this.detailDisplay = true;
		this.detail = col;
	}
	// -
	choose(i, data) {
		if (this.checkBox) {
			this.chooseData.push(data)
		} else {
			this.chooseData.forEach((item, i) => {
				if (item == data) {
					this.chooseData.splice(i, 1)
				}
			});
		}
	}
	//-
	leftMoveAll() {
		let stu = true;
		let temp = [];
		let temp1 = [];
		let arrsright = [];
		this.tableList2.forEach((item, i) => {
			if (stu) {
				arrsright.push(i)
				arrsright.sort(function (a, b) { return b - a; })
				this.tableList1.push(this.tableList2[i])
			}
		});
		if (stu) {
			arrsright.forEach((item, i) => {
				this.tableList2.splice(item, 1)
			});
			temp = _.clone(this.tableList1);
			this.tableList1 = _.clone(temp);
			temp1 = _.clone(this.tableList2);
			this.tableList2 = _.clone(temp1);
			this.checkBox = [];
		}
	}

	//
	leftMove() {
		let temp = [];
		let temp1 = [];
		let arrsleft = [];
		this.checkBox1.forEach(item => {
			arrsleft.push(item)
			arrsleft.sort(function (a, b) { return b - a; })
			this.tableList1.push(this.tableList2[item])
		});
		arrsleft.forEach((item, i) => {
			this.tableList2.splice(item, 1)
		});
		temp = _.clone(this.tableList2)
		this.tableList2 = _.clone(temp)
		temp1 = _.clone(this.tableList1)
		this.tableList1 = _.clone(temp1)
		this.checkBox1 = [];
	}

	//
	rightMove() {
		let stu = true;
		let temp = [];
		let temp1 = [];
		let arrsright = [];
		this.checkBox.forEach((item, i) => {
			this.tableList2.forEach(item_2 => {
				if (item_2.drId == this.tableList1[item].drId) {
					stu = false;
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '', detail: "" });
					this.checkBox = [];
				}
			})
			if (stu) {
				arrsright.push(item)
				arrsright.sort(function (a, b) { return b - a; })
				this.tableList2.push(this.tableList1[item])
			}
		});
		if (stu) {
			arrsright.forEach((item, i) => {
				this.tableList1.splice(item, 1)
			});
			temp = _.clone(this.tableList2);
			this.tableList2 = _.clone(temp);
			temp1 = _.clone(this.tableList1);
			this.tableList1 = _.clone(temp1);
			this.checkBox = [];
		}
	}
	//-
	rightMoveAll() {
		let stu = true;
		let temp = [];
		let temp1 = [];
		let arrsright = [];
		this.tableList1.forEach((item, i) => {
			this.tableList2.forEach(item_2 => {
				if (item_2.drId == this.tableList1[i].drId) {
					stu = false;
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '', detail: "" });
					this.checkBox = [];
				}
			})
			if (stu) {
				arrsright.push(i)
				arrsright.sort(function (a, b) { return b - a; })
				this.tableList2.push(this.tableList1[i])
			}
		});
		if (stu) {
			arrsright.forEach((item, i) => {
				this.tableList1.splice(item, 1)
			});
			temp = _.clone(this.tableList2);
			this.tableList2 = _.clone(temp);
			temp1 = _.clone(this.tableList1);
			this.tableList1 = _.clone(temp1);
			this.checkBox = [];
		}

	}

	//
	reset() {
		if (this.activeIndex == 0) {
			this.fileTemplateCreateBean.dtName = '';
			this.fileTemplateCreateBean.fileTemplateCate = '';
			this.fileTemplateCreateBean.dtDescription = '';
			this.fileTemplateCreateBean.dtEntity = '';
		} else if (this.activeIndex == 1) {
			this.list.forEach(element => {
				element.daWilllose = '';
				element.daUpdate = '';
				element.daQuery = '';
				element.daAnotherField = '';
				element.daEntityField = '';
				element.daFieldMapping = '';
				element.daDescription = '';
			});
		} else if (this.activeIndex == 2) {
			this.drName = '';
			this.drEntityField = '';
			this.leftClick();
			this.rightClick();
		}
	}

	//
	returnBtn() {
		setTimeout(() => {
			window.history.go(-1);
		}, 500)
	}

	//
	prev() {
		if (this.fileTemplateCreateBean.dtType == '03') {
			if (this.activeIndex == 1) {
				this.activeIndex = this.activeIndex - 1;
				this.vlaue = this.activeIndex;
				this.showStep = false;
			} else {
				this.activeIndex = this.activeIndex - 1;
				this.vlaue = this.activeIndex;
			}
		} else if (this.fileTemplateCreateBean.dtType == '04') {
			if (this.activeIndexs == 1) {
				this.activeIndexs = this.activeIndexs - 1;
				this.vlaue = this.activeIndexs;
				this.showStep = false;
			}
		}
	}

	//
	next() {
		if (this.fileTemplateCreateBean.dtType != '04') {
			if (this.activeIndex == 0) {
				if (this.flagUpdate || !this.showStep) {
					if (!this.fileTemplateCreateBean.dtName) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
						return;
					}
					if (!this.fileTemplateCreateBean.fileTemplateCate) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
						return;
					}
					if (!this.fileTemplateCreateBean.dtEntity) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
						return;
					}
					this.fileTemplateCreateBean.dtId = this.dtId;
					this.fileCenterHttpService.updateDocumentTemplate(this.fileTemplateCreateBean).subscribe(data => {
						if (data.returnCode.code == 'success') {
							this.activeIndex = this.activeIndex + 1;
							this.vlaue = this.activeIndex;
							this.temPlateClick();
							this.msgs = [];
							this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
						}
					}, error => {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
					})

				} else {
					if (!this.fileTemplateCreateBean.dtName) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
						return;
					}
					if (!this.fileTemplateCreateBean.fileTemplateCate) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
						return;
					}
					if (!this.fileTemplateCreateBean.dtEntity) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
						return;
					}
					this.fileCenterHttpService.createDocumentTemplate(this.fileTemplateCreateBean).subscribe(data => {
						if (data.returnCode.code == 'success') {
							this.dtId = data.dtId;
							this.activeIndex = this.activeIndex + 1;
							this.vlaue = this.activeIndex;
							this.temPlateClick();
							this.msgs = [];
							this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
						}
					}, error => {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
					})
				}
			} else if (this.activeIndex == 1) {//
				let arr = [];
				let obj = {};
				this.list.forEach((element, index) => {
					element['dtId'] = this.dtId;
					element['daNo'] = index + 1;
				});
				let param = {
					daList: this.list
				}
				this.fileCenterHttpService.createDocumentAttributes(param).subscribe(data => {
					if (data.returnCode.code == 'success') {
						this.activeIndex = this.activeIndex + 1;
						this.vlaue = this.activeIndex;
						this.leftClick();
						this.rightClick();
						this.msgs = [];
						this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
					} else {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
					}
				}, error => {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '', detail: '' });
				})
			} else if (this.activeIndex == 2) {//
				let arr = [];
				this.tableList2.forEach(element => {
					arr.push(element.drId);

				});
				let param = {
					dtId: this.dtId,
					drId: arr.toString(),
				}
				if (this.tableList2.length > 0) {
					this.fileCenterHttpService.createDocumentRulesRelationship(param).subscribe(data => {
						if (data.returnCode.code == 'success') {
							this.activeIndex = this.activeIndex + 1;
							this.vlaue = this.activeIndex;
							this.msgs = [];
							this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
						}
					}, error => {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
					})
				} else if (this.tableList2.length == 0) {
					this.activeIndex = this.activeIndex + 1;
					this.vlaue = this.activeIndex;
				}

			} else if (this.activeIndex == 3) {

			}
		} else if (this.fileTemplateCreateBean.dtType == '04') {
			if (this.activeIndexs == 0) {
				if (this.flagUpdate || !this.showStep) {
					if (!this.fileTemplateCreateBean.dtName) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
						return;
					}
					if (!this.fileTemplateCreateBean.fileTemplateCate) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
						return;
					}
					if (!this.fileTemplateCreateBean.dtEntity) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
						return;
					}
					this.fileTemplateCreateBean.dtId = this.dtId;
					this.fileCenterHttpService.updateDocumentTemplate(this.fileTemplateCreateBean).subscribe(data => {
						if (data.returnCode.code == 'success') {
							this.activeIndexs = this.activeIndexs + 1;
							this.vlaue = this.activeIndexs;
							this.temPlateClick();
							this.msgs = [];
							this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
						}
					}, error => {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
					})

				} else {
					if (!this.fileTemplateCreateBean.dtName) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
						return;
					}
					if (!this.fileTemplateCreateBean.fileTemplateCate) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
						return;
					}
					if (!this.fileTemplateCreateBean.dtEntity) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
						return;
					}
					this.fileCenterHttpService.createDocumentTemplate(this.fileTemplateCreateBean).subscribe(data => {
						if (data.returnCode.code == 'success') {
							this.dtId = data.dtId;
							this.activeIndexs = this.activeIndexs + 1;
							this.vlaue = this.activeIndexs;
							this.temPlateClick();
							this.msgs = [];
							this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
						}
					}, error => {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: '' });
					})
				}
			} else if (this.activeIndexs == 1) {//
				let arr = [];
				let obj = {};
				this.list.forEach((element, index) => {
					element['dtId'] = this.dtId;
					element['daNo'] = index + 1;
				});
				let param = {
					daList: this.list
				}
				this.fileCenterHttpService.createDocumentAttributes(param).subscribe(data => {
					if (data.returnCode.code == 'success') {
						// this.activeIndexs = this.activeIndexs + 1;
						// this.vlaue = this.activeIndexs;
						// this.leftClick();
						// this.rightClick();
						this.msgs = [];
						this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
					} else {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
					}
				}, error => {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '', detail: '' });
				})
			}
		}
	}

	//
	toDelete(i) {
		this.list.splice(i, 1);
		this.list = _.clone(this.list);
	}
	//
	temPlateClick() {
		let param = {
			dtId: this.dtId
		}
		this.fileCenterHttpService.findDocumentAttributes(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				if (data.resultList) {
					this.list = data.resultList;
				} else {
					this.list = [];
				}
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}
	//
	save() {
		let param = {
			dtId: this.dtId,
		}
		this.fileCenterHttpService.createOrStoreDocumentUseRange(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}

	//-
	leftClick() {
		let param = {
			dtId: this.dtId,
			drEntityField: this.drEntityField,
			drName: this.drName
		}
		this.fileCenterHttpService.queryDocumentRulesUnusedAssociation(param).subscribe(data => {//
			if (data.returnCode.code == 'success') {
				this.tableList1 = data.ruleList;
				// this.tableList1 = this.commfunc.listToLabelvalue(data.ruleList, "drName", "drId");
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}
	//-
	rightClick() {
		this.fileCenterHttpService.queryDocumentRulesAssociation({ dtId: this.dtId }).subscribe(data => {//
			if (data.returnCode.code == 'success') {
				if (data.ruleList) {
					this.tableList2 = data.ruleList;
				} else {
					this.tableList2 = [];
				}
				// this.tableList2 = this.commfunc.listToLabelvalue(data.ruleList, "drName", "drId");
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}

	//
	outValue(event) {
		if (event == false) {
			this.custChooseDisplay = event;
		} else {
			event.forEach(element => {
				element['daType'] = element.filedType;
				element['daLength'] = element.filedLen;
			});
			console.log(...event);
			this.list.push(...event);
			let result = [];
			let obj = {};
			for (let i = 0; i < this.list.length; i++) {
				if (!obj[this.list[i].daField]) {
					result.push(this.list[i]);
					obj[this.list[i].daField] = true;
				}
			}
			this.list = _.clone(result);
			this.custChooseDisplay = false;
			if (this.list) {
				this.list.forEach(element => {
					element['daWilllose'] = '0';
					element['daUpdate'] = '0';
					element['daQuery'] = '0';
				});
			}
		}
	}

	getIndex(list, item) {
		let index = -1;
		for (let i = 0; i < list.length; i++) {
			if (list[i].daField == item.daField) {
				index = i;
				break;
			}
		}
		return index
	}
	//
	up(car) {
		let carList = JSON.parse(JSON.stringify(this.list));
		let i = this.getIndex(carList, car);
		if (i != -1 && i != 0) {
			let a = carList[i - 1];
			carList[i] = a;
			carList[i - 1] = car;
		}
		this.list = carList;
	}

	//
	down(car) {
		let carList = JSON.parse(JSON.stringify(this.list));
		let i = this.getIndex(carList, car);
		if (i < carList.length - 1) {
			let a = carList[i + 1];
			carList[i] = a;
			carList[i + 1] = car;
		}
		this.list = carList;
	}

	//
	updateDetail() {
		this.fileCenterHttpService.findDocumentTemplateDetails({ dtId: this.dtId }).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.fileTemplateCreateBean = data.docuTem;
				this.list = data.docuTem.attrs;
				console.log(this.list)
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}
	change() {
		setTimeout(() => {
			let compWidth = document.getElementById('compId');
			let compLi = compWidth.getElementsByTagName('li');
			for (let i = 0; i < compLi.length; i++) {
				compLi[i].style.width = `${100 / compLi.length}%`;
			}
		})
	}
}