import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { FileCenterHttpService } from '../http/file-center.http.service';
import { FiledListBean } from './bean/field-list.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'field-list',
    templateUrl: './field-list.component.html',
    styleUrls: ['./field-list.component.scss'],
    providers: [ConfirmationService, FileCenterHttpService]
})

export class FieldListComponent implements OnInit {
    addDisplay: boolean = false;
    mesg: any;//
    msgs: Message[] = [];//
    showFlag: string;
    headerTitle: string;
    filedListBean: FiledListBean = new FiledListBean();
    list: any = [];
    total: number;
    first: number = 0;//
    code: any;
    FiledType: any = [];//
    FiledView: any = [];//
    FiledStatus: any = [];//
    inValue1: any;
    inValue2: any;
    constructor(
        public confirmationService: ConfirmationService,
        public fileCenterHttpService: FileCenterHttpService,
        public commfunc: Commfunc
    ) {
        this.codeValues();
        this.FiledView = this.code['filedView1'];//
        this.FiledStatus = this.code['FiledStatus'];//
        this.FiledType = this.code['FiledType'];//
    }

    ngOnInit() {
        this.filedListBean.pageNum = 1;
        this.filedListBean.pageSize = 10;
        this.query()
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    //
    query() {
        this.filedListBean.pageNum = 1;
        this.filedListBean.pageSize = 10;
        this.queryClick()
    }
    queryClick() {
        this.fileCenterHttpService.queryAttrItemList(this.filedListBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.total = data.totalNum;
                this.list = data.fieldsList;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })
    }
    //
    reset() {
        this.filedListBean = new FiledListBean();
        this.filedListBean.pageNum = 1;
        this.filedListBean.pageSize = 10;
        this.querySecleReset();
    }
    //
    querySecleReset() {
        this.filedListBean.pageSize = 10;
        this.filedListBean.pageNum = 1;
        this.first = 0;
    }
    //
    paginate(event) {
        //
        this.filedListBean.pageSize = Number(event.rows);
        //
        let currentPage = event.page + 1;
        this.filedListBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.filedListBean.pageSize;
        this.queryClick();//
    }
    delete(item) {
        this.confirmationService.confirm({
            message: ''+item.daField+'?',
            accept: () => {
                let param = {
                    daField: item.daField,
                    filedStatus:'1',
                }
                this.fileCenterHttpService.createOrStoreAttrItem(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                        this.queryClick();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                })
            }
        });
    }
    //
    add() {
        this.addDisplay = true;
        this.showFlag = '1';
        this.headerTitle = '';
    }
    //
    outValueAdd(param) {
        this.addDisplay = param;
        this.queryClick();
    }
    //
    update(item) {
        this.addDisplay = true;
        this.showFlag = '2';
        this.headerTitle = '';
        this.inValue2 = item.daField;
    }

    //
    outValueUpdate(event) {
        this.addDisplay = event;
        this.queryClick();
    }

    //
    check(item) {
        this.addDisplay = true;
        this.showFlag = '3';
        this.headerTitle = '';
        this.inValue1 = item.daField;
    }

    //
    toNewUse (item) {
        item['filedStatus']='0';
        this.fileCenterHttpService.createOrStoreAttrItem(item).subscribe(data=>{
            if (data.returnCode.code == 'success') {
                this.queryClick();
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })
    }
}