import { Component, OnInit } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { Message, MenuItem, SelectItem } from 'primeng/primeng';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { tableMessage } from '../../../../../../@uisftech/common/constant/config';
import { FileTemplateBean } from '../../bean/file-template.bean';
import * as _ from 'lodash';
import { FileCenterHttpService } from '../../http/file-center.http.service';

@Component({
	selector: 'file-template-detail',
	templateUrl: './file-template-detail.component.html',
	styleUrls: ['./file-template-detail.component.scss'],
	providers:[FileCenterHttpService]
})
/**
 * 
 */
export class FileTemplateDetailComponent implements OnInit {

	mesg: any;//
	msgs: Message[] = [];//
	tabMesg: string = tableMessage;//
	fileTemplateBean: FileTemplateBean = new FileTemplateBean();//
	fileType: any;//
	items: MenuItem[];//
	activeIndex: number;
	vlaue: any;//div
	flag: number;//
	Flag: boolean = false;//
	code: any;//
	oneChooseList: any = [];//-
	allChooseList: boolean = false;//
	detailDisplay: boolean = false;//
	detail: any = {};//
	arr: any = [];//
	tableList1: any = [];//-
	tableList2: any = [];//-
	aaa: any;
	bbb: SelectItem[];
	ccc: any = [];
	dtId:string;//id
	dataMap:any={};
	dataList1:any=[];
	dataList2:any=[];
	dataList3:any=[];
	Judge:any=[];//
	FileTemplateCate:any=[];
	DtType:any=[];
	FiledType:any=[];
	constructor(
		public commfunc: Commfunc,
		public router: Router,
		public fileCenterHttpService:FileCenterHttpService,
		public activatedRoute:ActivatedRoute
	) {
		this.mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		this.codeValues() //
		this.Judge=this.code['JUDGE'];//
		this.FileTemplateCate=this.code['FileTemplateCate'];//
		this.DtType=this.code['DTTYPE'];//
		this.FiledType=this.code['FiledType'];//
	}

	//
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code
	}

	ngOnInit() {
		this.dtId = this.activatedRoute.snapshot.params['dtId'];
		this.echo();
	}


	//
	echo () {
		let param = {
			dtId:this.dtId
		}
		this.fileCenterHttpService.findDocumentTemplateDetails(param).subscribe(data=>{
			if (data.returnCode.code == 'success') {
				this.dataMap=data.docuTem;
				this.dataList2=data.docuTem.rules;
				this.dataList1=data.docuTem.attrs;
				this.dataList3=data.docuTem.useRanges;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        },error=>{
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail:''});
        })
	}

	//
	returnBtn() {
		setTimeout(() => {
			window.history.go(-1);
		}, 500)
	}


}